.card{
   width: 190px;
   display: flex;
   flex-direction: column;
   align-items: center;
   background-color: white;
   border-radius: 10px ;
   overflow: hidden;
   padding: 0 0 10px 0 ;
}

.card h3{
   font-size: 1.4rem;
   color: var(--headbg);
   margin: 10px 0;
   padding: 0 6px;
   text-align: center;
}

.card button{
   outline: none;
   border: none;
   font-size: .7rem;
   color: whitesmoke;
   cursor: pointer;
   font-weight: bold;
   background: var(--bbottom);
   padding: 6px 9px;
   border-radius: 9px;
   cursor: pointer;
   transition: all .4s ease;
}

.card img{
   width: 100%;
}

.popup{
   position: absolute;
   border-radius: 12px;
   overflow: hidden;
   /* border: 2px solid var(--bbottom); */
   z-index: 30;
}


.popup .popup-content p{
   padding: 10px;
   color: rgb(255, 255, 255);
   font-size: 1rem;
   font-weight: bold;
   text-align: justify;
   line-height: 1.4;
}


.popup .popup-content button{
   margin-bottom:10px ;
}