
.main{
   position: relative;
   width: 100%;
   display: flex;
   justify-content: center;
}

.main .mainContainer{
   position: absolute;
   top: -75px;
   width: 450px;
   padding: 10px 0;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-self: center;
   text-transform: uppercase;
   color: var(--headbg);
   background: rgb(255, 255, 255);
   border-radius: 10px;
   box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.26);
}
   .main .mainContainer h1{
   font-size: 2rem;
   margin: 0;
}
.mainContainer h2{
   margin: 0;
   color: var(--bbottom);
}

@media screen and (max-width: 1000px) {
   .main .mainContainer h1{
      font-size: 1.8em;
   }
}

@media screen and (max-width: 640px) {

   .main .mainContainer{
      position: absolute;
      width: calc(100% - 7%);
   }
}