

.i-head{
   height: 80vh;
   background: url('../../images/etudiant1.jpg');
   background-size: cover;
   background-position: center;
}
.i-header{
   position: relative;
   height: 100%;
   padding: 10px 20px;
   background: var(--head);
}

.i-header .logo>img{
   width: 70px;
}

.i-header > .i-text{
   width: 100%;
   height: calc(100% - 120px);
   display: flex;
   justify-content: center;
   align-items: center;
}
