*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root{
  --bbottom:#f84622;
  --bg:#f84622;
  --head:#0660a9bf;
  --headbg:rgb(6, 96, 169);
  --hoverB:rgb(13, 56, 92);
  --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
  --orangeCard: rgba(252, 166, 31, 0.45);
  --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}


.logo img{
  border-radius: 20px;
  box-shadow: var(--boxShadow);
  width: 60px;
}
