.animate-on-scroll {
   opacity: 0;
   transform: translatey(50px);
   transition: 0.6s ease-in-out, 
}

.animate-on-scroll.is-visible {
   opacity: 1;
   transform: translateY(0);
} 



/* formation*/
.sectionTwo{
   width: 100%;
   position: relative;
   margin-top: 120px;
   background: url('../../images/fin.jpg');
   background-size: cover;
   background-position: center;
   color: white;
}

.sectionTwo .protector{
   margin-bottom: 130px;
}

.sectionTwo .sonThree{
   padding: 120px 3.4%;
   background: var(--head);
}

.sectionTwo .hContent h1{
   color: white;
}

.sectionTwo .poles{
   width: 100%;
   padding-top: 120px;
   display: flex;
   gap: 6%;
}

.poles .poletech,
.poles .polecom{
   flex: 45%;
   width: 100%;
 }
 
 .poles .poletech,
 .head .content,
 .poles .polecom{
   display: flex;
   flex-direction: column;
   align-items: center;
 }
 
 
 .head .content h1{
   font-size: 1.5rem;
   text-transform: uppercase;
 }
 .poles .gridcard{
   padding-top: 50px;
   width: 100%;
   display: grid;
   grid-template-columns: repeat(3, 1fr);
 }
 
 /* grid concernant les filieres */
 
 .grids .grid {
   margin: 20px;
   background: white;
   text-align: center;
   color: var(--head);
   border-radius: 12px;
   overflow: hidden;
   transition: all .3s ease;
}


.grids .grid img{
   width: 100%;
}

.grids .grid h3{
   padding: 10px;
}

/* fin de la section formation */


 /* responsive  */ 

@media screen and (max-width: 1320px) {

   .poles .gridcard{
      grid-template-columns: repeat(2, 1fr);
   }
}
@media screen and (max-width: 1170px) {
   .sectionTwo .poles{
   gap: 3%;
   }
}

@media screen and (max-width: 1000px) {
   

   .hContent h1 {
   font-size: 1.8em;
   }
   
   .sectionTwo .sonThree{
      padding: 120px 2%;
   }
}

@media screen and (max-width: 950px) {
   
   .poles .gridcard{
      grid-template-columns: repeat(3, 1fr);
      gap: 40px;
   }

   .sectionTwo .poles{
      flex-direction: column;
      gap: 5%;
   }

   .sectionTwo .poles .polecom{
      margin-top: 60px;
   }
}


@media screen and (max-width: 850px) {
   .inscription form{
      width: 97%;
   }
}

@media screen and (max-width: 700px) {
   .poles .gridcard{
      grid-template-columns: repeat(2, 1fr);
   }

   .card{
      width: 250px;
   }

}

@media screen and (max-width: 640px) {
   
   .card{
      width: 300px;
   }
   .poles .gridcard{
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 0  auto;
   }

   .inscription form .usersInfo{
      flex-direction: column;
   }


.sectionTwo .poles{
   padding-top: 70px;
}

}

@media screen and (max-width: 450px) {

   .presentation h1{
      font-size: 1.7rem;
   }
   .main .mainContainer h1,
   .hContent h1 {
      font-size: 1.4em;
   }
}

@media screen and (max-width: 350px) {
   .card{
      width:80%;
   }
}