.firstSection{
   margin-top: 230px;
}

.firstSection .Fson{
   padding: 0 7%;
}


.firstSection .aboutContainer{
   display: flex;
   padding-top: 50px;
   gap: 5%;
}

.aboutContainer .pic{
   flex: 40%;
   width: 100%;
   display: flex;
   align-items: center;
   justify-content: center;
   overflow: hidden;
   border-radius: 12px;
} 

.aboutContainer .pic .gstory {
   display: flex;
   justify-content: center;
   width: 80%;
   margin: 0 auto;
}

.aboutContainer .stories{
   flex: 40%;
   width: 100%;
   font-size: 1.2rem;
   line-height: 1.5;
   display: flex;
   justify-content: center;
}

.aboutContainer .stories p{
   width: 100%;
   text-align: justify;
}

@media screen and (max-width: 1000px) {

   .firstSection .Fson{
      padding: 0 2%;
   }

}

@media screen and (max-width: 950px) {


   .aboutContainer .pic >div{

      width: 100%;
      height: 60vh;
   }

   .aboutContainer .stories{

      margin-top: 20px;
      text-align: justify;
   }

   .firstSection .aboutContainer{
      flex-direction: column;
      gap: 1%;
   }
}