.headerText{
   width: 100%;
   display: flex;
   justify-content: center;
}

.headerText .hContent{
   display: flex;
   flex-direction: column;
   align-items: center;
}

.hContent .botm{
   display: flex;
   align-items: center;
}

.hContent .botm .line{

   width: 70px;
   height: 2px;
   background: var(--bbottom);
}

.hContent h1{
   text-transform: uppercase;
   color: var(--headbg);
   font-size: 2rem;
   box-shadow: var(--boxShadow);
   text-align: center;
   margin: 0;
}

.hContent div i{
   color: var(--bbottom);
}
