

.callus{
   position: relative;
   margin-top: 110px;
   background: url('../../images/study.jpg');
   background-size: cover;
   background-position: top;
}

.callus .callContainer{
   padding-top: 110px;
   width: 100%;
   background: var(--head);
   color: white
}

.callContainer .hContent h1{
   color:white ;
}

.callContainer .formPart{

   width: 100%;
   padding: 0 7%;
   display: flex;
   align-items: center;
   justify-content: center;
}

.formPart .toCenter{
   width: 100%;
   display: flex;
   align-items: center;
   flex-direction: column;
   gap: 5%;
}

@media screen and (max-width: 770px) {

   .callContainer .formPart{
      padding: 0 2%;
   }
}
