
footer{
   position: relative;
   width: 100%;
   height: auto;
   margin: 0;
   display: flex;
   flex-direction: column;
   align-items: center;
   background: url('../../images/study.jpg');
   background-size: cover;
   background-repeat: no-repeat;
   background-position: center;
}


footer img{
   width: 120px;
   border-radius: 20px;
}

.f-part{
   width: 100%;
   height: 100%;
   display: flex;
   flex-direction: column;
   align-items: center;
   background: var(--head);
   background-size: 100% 100%;
   padding: 60px 0;
   padding-left: 3.5%;
   padding-right: 3.5%;
}

.f-part .hContent h1, 
footer p{
   color: white;
}

.f-part .f-grids{
   width: 100%;
   display: grid;
   grid-template-columns: repeat(3, 1fr);
   gap: 10%;
   padding-top: 70px;
   background: none;
}

.f-part .f-grids .f-grid{
   width: 100%;
   display: flex;
   justify-content: center;
   background: none;
}

.f-grid .f{
   background: none;
   width: 50%;
   display: flex;
   flex-direction: column;
   align-items: center;
   gap: 10px;
}

.f-grid .f .ic{
   color: white;
   font-size: 3rem;
}

.f-grid .f .icons{
   color: var( --bbottom);
   font-size: 1.7rem;
}

.f-grid .f .icons:nth-child(2){
   margin: 18px 0;
}


.f-grid #net div:nth-child(1){
   width: 80%;
   display: grid;
   grid-template-columns: repeat(4, 1fr);
   gap: 20px;
}

.f-grid .reseaux .icon{
   font-size: 2rem;
}

@media screen and (max-width: 1150px) {
   .f-grid .f{
      width: 80%;
   }
}

@media screen and (max-width: 950px) {

/*       
   .f-part .f-grids{
      grid-template-columns: repeat(1, 1fr);
   } */
   
   .f-part .f-grids .f-grid{
      margin-top: 0;
   }
}

@media screen and (max-width: 780px) {

   .f-part .f-grids{
      grid-template-columns: repeat(1, 1fr);
   } 
   

}