
form{
   padding-top: 100px;
   width: 100%;
   display: flex;
   flex-direction: column;
   align-items: center;
   margin-bottom: 70px;
}

form input, 
form textarea,
form select{
   outline: none;
   border: none;
   background: #ecececd7;
   font-size: 1.1rem;
   border-bottom: 3px solid transparent;
   transition: all .4s ease-in;
   cursor: pointer;
}

form input:focus{

   border-bottom: 3px solid var(--bbottom);
}

form .usersInfo{
   width: 100%;
   display: flex;
   gap: 20px;
}

form .usersInfo .name{
   width: 100%;
}

form label{
   font-size: 1.2rem;
   font-weight: 650;
   color: #ffffff;
   margin-bottom: 9px;
   padding-left: 10px;
}

.name p,
.usermessage p,
.usermail p{
   font-size:1rem;
   font-weight: bold;
   color: var(--bbottom);
}
.usersInfo .name input,
 .usermail input,
 .usersInfo .f-choice select{
   width: 100%;
   padding: 10px 8px;
   border-radius: 8px;
}

form .usermail{
   width: 100%;
   display: flex;
   flex-direction: column;
   margin: 10px 0;
}

form .usermessage{
   width: 100%;
}

form textarea{
   margin-top: 9px;
   width: 100%;
   padding: 10px;
   outline: none;
   border: none;
   border-bottom: 2px solid transparent;
   transition: all .3s ease;
}

#spin{
   color: #ffffff;
   margin-top: 10px;
}
form textarea:focus{

   border-bottom: 2px solid var(--debut);
}

form button{
   margin-top: 20px;
   outline: none;
   border: none;
   font-size: 1.2rem;
   color: whitesmoke;
   font-weight: bold;
   background: var(--bbottom);
   padding: 10px 15px;
   border-radius: 9px;
   cursor: pointer;
   border: 2px solid transparent;
   transition: all .4s ease;
}

form button:hover{
   border: 2px solid white;
   background: var(--head);
   color: white;
}

@media screen and (max-width: 1000px) {

   form{
      width: 100%;
   }

}

