/* header.css */
nav a {
   scroll-behavior: smooth;
}

header {
   background: url('../../images/fin.jpg');
   background-size: 100% 100%;
   color: white;
}

.header {
   background: var(--head);
}

.headPage {
   position: fixed;
   margin-top: -1px;
   width: 100%;
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 14px 10px;
   z-index: 100;
}

.headPage.active {
   background: var(--headbg);
}

.headPage a {
   font-size: 1.2rem;
   font-weight: bold;
   color: white;
   text-decoration: none;
   padding: 10px;
   border-radius: 4px;
   border-bottom: 2px solid transparent;
   transition: all .4s ease;
}

.headPage a:hover {
   background: var(--hoverB);
   border-bottom: 2px solid var(--bbottom);
}

.headPage .logo img {
   max-width: 100px;
}

.headPage .links {
   display: flex;
   align-items: center;
   gap: 30px;
}

.header .headBody {
   width: 100%;
   height: 90%;
   padding: 0 7%;
   display: flex;
   justify-content: start;
   align-items: center;
}

.headBody .presentation {
   margin: 190px 0;
}

.presentation h1 {
   font-size: 2.5rem;
   line-height: 1.3;
}

.presentation h1 span {
   color: var(--bbottom);
}

.presentation p {
   font-size: 1.1rem;
   line-height: 1.3;
   margin: 20px 0;
}

/* menu mobile */
#navbar .menu-button {
   cursor: pointer;
   display: none;
}

.nav-mobile .links {
   width: 100%;
   left: 0;
   top: 75px;
   padding: 20px 0;
   background: black;
   position: absolute;
   display: flex;
   flex-direction: column;
   align-items: center;
   gap: 9px;
}

.noMobile {
   display: flex;
}

/* fin */

.presentation div {
   width: fit-content;
   padding: 10px 4px;
   background: var(--bbottom);
   border-radius: 3px;
}

.presentation a {
   color: white;
   text-decoration: none;
   margin-top: 40px;
   border: 2px solid transparent;
   border-radius: 3px;
   padding: 5px;
   font-size: 1.1rem;
   transition: all .3s ease-in-out;
}

.presentation a:hover {
   border: 2px solid wheat;
}

@media screen and (max-width: 1000px) {
   .headPage {
   padding: 20px 20px;
   }

   .presentation {
   display: flex;
   flex-direction: column;
   align-items: center;
   }

   .headPage .logo img {
   max-width: 90px;
   }

   .headPage .links {
   gap: 10px;
   }
}

@media screen and (max-width: 950px) {
   .header {
   background-size: cover;
   background-position: bottom;
   }

   .header .headBody {
   height: 86%;
   padding: 0 3.5%;
   justify-content: center;
   }

   #accueil {
   position: relative;
   top: 1px;
   }

   .presentation .txt {
   text-align: center;
   }

   .headPage .noMobile {
   display: none;
   }

   #navbar .menu-button {
   outline: none;
   border: none;
   padding: 9px;
   background: none;
   display: block;
   }
}

#navbar .menu-button .menu {
   color: var(--bbottom);
   font-size: 2rem;
}

@media screen and (max-width: 700px) {
   header {
   background-size: cover;
   background-position: bottom;
   }
}

@media screen and (max-width: 550px) {
   .presentation h1 {
   font-size: 1.7rem;
   text-align: center;
   }

   .presentation p {
   font-size: 1rem;
   text-align: center;
   }
}
